import {
  ComponentProps,
  Streamlit,
  withStreamlitConnection,
} from "streamlit-component-lib"
import React, { useEffect } from "react"

const Header = ({ args }: ComponentProps) => {
  const { title, img_src, author_url, author } = args

  useEffect(() => Streamlit.setFrameHeight())

  return (
    <div>
      <h1>{title}</h1>
      <div className="authorContainer">
        <a
          className="author"
          href={author_url}
          target="_blank"
          rel="noopener noreferrer"
        >
          <span>
            <img
              height="32"
              width="32"
              src={img_src}
              style={{ borderRadius: 100, marginRight: 12 }}
            />
          </span>
          {author}
        </a>
        &nbsp;using&nbsp;
        <a
          href="https://github.com/messari/subgraphs"
          target="_blank"
          rel="noopener noreferrer"
        >
          Messari Subgraphs
        </a>
      </div>
    </div>
  )
}

export default withStreamlitConnection(Header)
